body {
    margin: 0;
}
  
.page {
    min-height: 100vh;
    display:flex;
    flex-direction: column;
}
  
.main {
    padding: 0 28px;
    min-height: 458px;
    flex-grow: 1;
    background-color: $color-white-three;
    @media all and (min-width: 768px){
      position: relative;
      min-height: 600px;
    }
}
  
.main__container {
    text-align: center;
    @media all and (min-width: 768px){
      display: flex;
      flex-direction: column;
      max-width: 419px;
      margin: 0 auto;
      margin-bottom: 287px;
    }
}

  .main__icons {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 24px;
    list-style: none;
    border-radius: 4px;
    border: 2px solid rgba(225, 115, 52, .21);
    width: 268px;
    margin: auto;
    padding: 15px 20px 10px;
}
  
.main__title {
    color: $color-dull-orange;
    font-size: 19px;
    font-family: $font-family-merri;
    font-weight: 900;
    text-align: center;
    padding: 0;
    @media all and (min-width: 768px){
      font-size: 24px;
      padding: 0 70px;
      margin: 0;
    }
}
  
.main__subtitle {
    color: $color-dark-green-blue;
    font-size: 13px;
    font-family: $font-family-open;
    text-align: center;
    @media all and (min-width: 768px){
      padding: 0 44px;
      font-size: 16px;
      margin: 4px auto 21px;
    }
}
  
.main__item {
    color: $color-gunmetal;
    font-size: 16px;
    font-family: $font-family-open;
    text-align: center;
}
  
.btn__comenzar {
    color: $color-white;
    font-size: 16px;
    font-family: $font-family-open;
    font-weight: 600;
    text-align: center;
    background-color: $color-dirty-blue;
    text-transform: uppercase;
    width: 129px;
    height: 42px;
    border-radius: 21px;
    border: 0px;
    margin: 39px auto 30px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    cursor:pointer;
    transition: box-shadow ease .7s;
      &:hover{
        box-shadow: 10px 10px 20px #b8b8b9;
      }
    @media all and (min-width: 768px){
      position: absolute;
      left: 50%;
      bottom: 59px;
      transform: translateX(-50%);
      margin: 0;
    }
}
  
.link__other{
    text-decoration: none;
}
  
.icon {
    color: $color-dull-orange;
    font-size: 28px;
    text-align: center;
    height: 26px;
    display: flex;
    justify-content: center;
}